import React, { PureComponent } from 'react';
import Types from 'prop-types';

import Select from '../select';

import { isDateValid, getMonthNames, explodeDateModel } from './utils';
import { DEFAULT_LOCALE } from './data';

const MonthBeforeDay = ['en-US', 'ja-JP'];
const DEFAULT_VALUES = { day: null, month: 0, year: null };

class DateInput extends PureComponent {
  static propTypes = {
    disabled: Types.bool,
    size: Types.oneOf(['sm', 'md', 'lg']),
    value: Types.oneOfType([Types.string, Types.instanceOf(Date)]),
    locale: Types.string,
    onChange: Types.func.isRequired,
  };

  static defaultProps = {
    disabled: false,
    size: 'md',
    value: null,
    locale: DEFAULT_LOCALE,
  };

  constructor(props) {
    super(props);
    const { value, locale } = props;
    const { day, month, year } = isDateValid(value) ? explodeDateModel(value) : DEFAULT_VALUES;

    this.state = {
      day,
      month,
      year,
      months: getMonthNames(locale),
      monthBeforeDay: MonthBeforeDay.indexOf(locale) > -1,
    };
  }

  static getDerivedStateFromProps(nextProps, state) {
    if (nextProps.value && isDateValid(nextProps.value)) {
      const { day, month, year } = explodeDateModel(nextProps.value);
      if (day !== state.day || month !== state.month || year !== state.year) {
        const { months } = state;
        nextProps.onChange({ day, month, year, months });
        return {
          ...state,
          ...explodeDateModel(nextProps.value),
        };
      }
    }

    if (nextProps.locale !== state.locale) {
      const { day, month, year } = state;
      const months = getMonthNames(nextProps.locale);
      nextProps.onChange({ day, month, year, months });
      return {
        ...state,
        monthBeforeDay: MonthBeforeDay.indexOf(nextProps.locale) > -1,
        locale: nextProps.locale,
        months,
      };
    }

    return null;
  }

  getSelectElement = () => {
    const { disabled, size } = this.props;
    const { month } = this.state;
    return (
      <div>
        <label htmlFor="tw-date-month" className="sr-only">
          Month
        </label>
        <Select
          id="tw-date-month"
          name="month"
          className="form-control tw-date-month"
          selected={{ value: month, label: this.state.months[month] }}
          onChange={selectedValue => this.handleMonthChange(selectedValue)}
          disabled={disabled}
          options={this.getMonthsOptions()}
          size={size}
        />
      </div>
    );
  };

  getMonthsOptions = () => {
    const options = [];

    this.state.months.forEach((month, index) => {
      options.push({ value: index, label: month });
    });
    return options;
  };

  handleDayChange = event => {
    const { checkedDay } = this.checkDate(event.target.value, this.state.month, this.state.year);

    this.setState({ day: checkedDay }, () => {
      const { day, month, year, months } = this.state;
      this.props.onChange({ day, month, year, months });
    });
  };

  handleMonthChange = selectedValue => {
    const { checkedDay } = this.checkDate(this.state.day, selectedValue.value, this.state.year);

    this.setState({ month: selectedValue.value, day: checkedDay }, () => {
      const { day, month, year, months } = this.state;
      this.props.onChange({ day, month, year, months });
    });
  };

  handleYearChange = event => {
    const { value } = event.target;
    const slicedYear = value.length > 4 ? value.slice(0, 4) : value;

    if (slicedYear.length === 4) {
      const { checkedDay, checkedYear } = this.checkDate(
        this.state.day,
        this.state.month,
        event.target.value,
      );

      this.setState({ day: checkedDay, year: checkedYear }, () => {
        const { day, month, year, months } = this.state;
        this.props.onChange({ day, month, year, months });
      });
    } else {
      this.setState({ year: slicedYear }, () => {
        const { day, month, months, year } = this.state;
        this.props.onChange({ day, month, year, months });
      });
    }
  };

  checkDate = (day = null, month = 0, year = null) => {
    let checkedDay = day;

    if (day && month) {
      const maxDay = new Date(year || 2000, month + 1, 0).getDate();
      checkedDay = day > maxDay ? maxDay : day;
    }
    if (day < 0) {
      checkedDay = 1;
    }
    if (day > 31) {
      checkedDay = 31;
    }

    return { checkedDay, checkedMonth: month, checkedYear: year };
  };

  render() {
    const { disabled, size } = this.props;
    const { day, year, monthBeforeDay } = this.state;

    return (
      <div className="tw-date">
        <div className="row">
          {monthBeforeDay && (
            <div className="col-sm-5 tw-date-month-column">{this.getSelectElement()}</div>
          )}

          <div className="col-sm-3 tw-date-day-column">
            <div className={`input-group-${size}`}>
              <label className="sr-only" htmlFor="tw-date-date">
                Day
              </label>
              <input
                id="tw-date-date"
                type="number"
                name="day"
                className="form-control tw-date-day"
                value={day || ''}
                onChange={event => this.handleDayChange(event)}
                placeholder="DD"
                disabled={disabled}
              />
            </div>
          </div>

          {!monthBeforeDay && (
            <div className="col-sm-5 tw-date-month-column">{this.getSelectElement()}</div>
          )}

          <div className="col-sm-4 tw-date-year-column">
            <div className={`input-group-${size}`}>
              <label className="sr-only" htmlFor="test">
                Year
              </label>
              <input
                type="number"
                name="year"
                className="form-control tw-date-year"
                placeholder="YYYY"
                value={year || ''}
                onChange={event => this.handleYearChange(event)}
                disabled={disabled}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DateInput;



