import { isExplodedDatePatternCorrect } from '../';

export const explodeDateModel = date => {
  const dateObj = typeof date === 'string' ? new Date(date) : date;

  const day = dateObj.getUTCDate();
  const month = dateObj.getUTCMonth();
  const year = dateObj.getUTCFullYear();

  return isExplodedDatePatternCorrect(year, month, day) ? { day, month, year } : null;
};



